/* Global */
@import url("https://fonts.googleapis.com/css2?family=DM+Sans:wght@400;500;700&display=swap");
body,
html {
  scroll-behavior: smooth;
  min-height: 100%;
  position: relative;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: rgb(109, 109, 109);
  line-height: 1.6;
  font-weight: 420;
  font-size: 15px;
}
p {
  margin: 0;
  padding: 0;
}
p::first-letter {
  text-transform: uppercase;
}
code {
  background-color: #e0e0e0;
  width: fit-content;
  padding: 5px;
  border-radius: 3px;
  overflow-x: scroll;
}
hr {
  border: none;
  border-bottom: 1px solid #dadada;
  margin: 0;
  width: 100%;
}
/* Wrappers */
.container {
  width: 1650px;
  margin: auto;
  margin-bottom: 60px;
  overflow: auto;
}
.left-pane {
  width: calc(1650px * 0.11);
  padding: 25px 0px;
  background-color: #ffffff;
  border-right: 1px solid #dadada;
  position: fixed;
  height: 100%;
  bottom: 20;
  top: 15;
  z-index: 0;
}
.middle-pane {
  float: right;
  width: 89%;
}
.right-pane {
  width: 24%;
  margin-top: 25px;
  float: right;
}
.sticky {
  position: sticky;
  top: 0;
}
.content {
  width: 75%;
  float: left;
  margin-bottom: 10px;
  padding: 20px 0px 0px 0px;
  box-sizing: border-box;
  border-right: 1px solid #dadada;
  background-color: #ffffff;
}
.bordered-sidebar {
  padding: 0px 0px 5px 0px;
  box-sizing: border-box;
  border-radius: 5px;
  border: 1px solid #dadada;
  background-color: rgb(253, 251, 226);
  box-shadow: 0px 1px 1px 1px #e6e6e6;
}
.signup-background {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgb(224, 224, 224);
}
.login-register-wrapper {
  width: 350px;
  margin: auto;
  margin-top: 100px;
  padding: 20px 20px 15px 20px;
  border-radius: 10px;
  box-sizing: border-box;
  box-shadow: 2px 2px 5px 2px rgb(206, 206, 206);
  background-color: white;
}
/* Header */
header {
  background-color: rgb(245, 245, 245);
  width: 100%;
  border-bottom: 1px solid #dadada;
  box-shadow: 0px 1px 4px 1px #cccccc;
  position: sticky;
  top: 0;
  z-index: 1;
}
.mid-content {
  width: 40%;
  background-color: blue;
}
.search-bar {
  width: 40%;
  background-color: white;
  box-sizing: border-box;
  border-radius: 3px;
  z-index: 0;
}
.search-icon {
  position: absolute;
  padding: 2px 7px;
  color: grey;
  z-index: -1;
  top: 8px;
  left: auto;
}
.header-container {
  width: 1650px;
  margin: auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
header .avatar {
  width: 30px;
  height: 30px;
  margin-right: 30px;
  object-position: top;
  object-fit: cover;
  border-radius: 3px;
}
header img {
  width: 35px;
  margin-right: 5px;
}
header .word {
  color: #252526;
}
header input {
  border: none;
  margin: none;
}
.header-toggle {
  display: none;
  float: right;
  flex-direction: row;
  align-items: center;
  transition: color 0.6s ease;
}
.main-nav {
  float: right;
  display: flex;
  align-items: center;
}

#mobile-nav {
  display: none;
  position: fixed;
  z-index: 2;
  top: 0;
  background-color: #25252638;
  cursor: pointer;
  width: 100%;
  height: 100%;
}
.mobile-nav {
  width: 270px;
  height: 100%;
  z-index: 9;
  background-color: white;
  border-right: 2px solid #d4d4d4;
  box-shadow: 0px 2px 5px 1px rgba(0, 0, 0, 0.267);
}
.display-off {
  display: none;
}
.mobile-header-content {
  width: 90%;
  margin: auto;
  margin-top: 10px;
  background-color: rgb(234, 249, 255);
  padding: 10px;
  box-sizing: border-box;
  border: 1px solid #adc0d6;
  border-radius: 3px;
}
footer {
  position: absolute;
  bottom: -80px;
  left: 0;
  text-align: center;
  padding: 20px;
  background-color: #252526;
  margin-top: 20px;
  width: 100%;
}
/* question classes */
.question-list {
  width: 100%;
  min-height: 101px;
  border-bottom: 1px solid #dadada;
}
.question-views-answers {
  margin: 0px 15px;
}
.QList-left {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  height: 100px;
}
.QnA-attribute {
  width: 33px;
  float: left;
}
.QList-content {
  margin: 0px 50px 0px 90px;
  min-height: 61px;
  border-right: 1px solid #dadada;
  padding-bottom: 40px;
}
.QList-attribute {
  width: 50px;
  float: right;
  min-height: 100px;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  margin-left: 5px;
}
.QList-solved {
  color: white;
  background-color: #417acf;
  padding-top: 10px;
  border-radius: 2px;
}
.QList-vote-count {
  width: 100%;
  text-align: center;
  float: left;
  font-size: x-small;
}
.QnA-content {
  margin-left: 50px;
}
.QnA-answered {
  width: 33px;
  height: 33px;
  padding: 7px;
  border: 1px solid #417acf;
  float: right;
  box-sizing: border-box;
  color: #80abec;
  cursor: pointer;
}
.QnA-vote,
.like {
  width: 100%;
  float: right;
  font-size: 20px;
  box-sizing: border-box;
  color: #dadada;
  cursor: pointer;
}
.dislike {
  transform: scaleX(-1);
}
.like:hover,
.liked,
.QnA-answered:hover {
  color: #417acf;
  transition-duration: 0.3s;
}
.dislike:hover,
.disliked {
  color: rgb(255, 49, 49);
  transition-duration: 0.4s;
}
.yes {
  color: white;
  background-color: #417acf;
  border: none;
  cursor: auto;
}
.vote-count {
  width: 100%;
  text-align: center;
  float: right;
  font-size: x-small;
}
.tag-wrapper {
  border: 1px solid #dadada;
  border-radius: 3px;
  width: 24%;
  margin: 4px;
  padding: 3px;
  box-sizing: border-box;
}
.tags-wrapper {
  display: flex;
  flex-wrap: wrap;
}

/* Margins */
.mb-2 {
  margin-bottom: 10px;
}
.mb-1 {
  margin-bottom: 5px;
}
/* Forms */
.input-group {
  margin-bottom: 25px;
}
.textfield {
  width: 100%;
  height: 30px;
  padding: 10px 8px;
  box-sizing: border-box;
  border: 1px solid #dadada;
  border-radius: 3px;
  outline: none;
}
.textfield:focus {
  border: 1px solid #5392da;
  box-shadow: 0px 0px 0px 3px #c0dbff;
  transition-duration: 0.3s;
  box-sizing: border-box;
}
legend {
  font-size: 18px;
  text-align: center;
}
fieldset {
  padding: 0;
  border: none;
  border-top: 1px solid #dadada;
}
.tag-search {
  border: none;
  width: 100%;
  flex: 80%;
  border-bottom: 1px solid #dadada;
  margin-left: 5px;
  outline: none;
}

/* Alerts */
.alert {
  height: fit-content;
  width: 100%;
  margin: 5px 0;
  padding: 3px 10px 6px 10px;
  box-sizing: border-box;
}
.alert-danger {
  background-color: #ffd0d0;
  border: 1px solid red;
  border-radius: 3px;
  color: rgb(158, 44, 44);
}
.alert-success {
  background-color: #d8ffd0;
  border: 1px solid rgb(43, 255, 0);
  border-radius: 3px;
  text-align: center;
  color: rgb(71, 158, 44);
}
/* Images */
.question-avatar {
  height: 35px;
  width: 35px;
  border-radius: 6px;
  object-fit: cover;
}
.profile-avatar {
  height: 120px;
  width: 120px;
  border-radius: 8px;
  object-fit: cover;
}
.profile-avatar-wrapper {
  width: 100%;
  float: left;
}
/* Notification */
#notification-wrapper {
  width: 400px;
  height: 500px;
  box-sizing: border-box;
  background-color: #ffffff;
  position: absolute;
  box-shadow: 0px 2px 7px 1px #00000036;
  right: 40px;
  z-index: 3;
  top: 1;
  right: 0;
}
#note-selector {
  position: absolute;
  background-color: red;
  padding: 5px 10px;
  z-index: 4;
  opacity: 0%;
  cursor: pointer;
}
.comment-answer {
  padding: 1px 5px;
  background-color: #dbdbdb;
  border-radius: 2px;
  margin-bottom: 5px;
  width: fit-content;
  font-size: 11px;
}
.new {
  color: white;
  background-color: #ee8723;
}
.new-notifications {
  font-size: xx-small;
  position: absolute;
  z-index: 3;
  padding: 0px 4px;
  color: white;
  background-color: #ff6600;
  border-radius: 2px;
  top: 3px;
  left: 13px;
}
.notification-btn {
  margin: 0;
  background-color: white;
  border: none;
  line-height: 1;
  width: 100%;
  text-align: left;
  padding: 10px 0px;
  border-bottom: 1px solid #dbdbdb;
}
.notification-btn:hover {
  cursor: pointer;
  background-color: #c2e3f7;
  transition-duration: 0.2s;
}
.note-new {
  background-color: #fff1e3;
}
.notification-attribute {
  float: left;
  width: 50px;
  font-size: 18px;
  margin-top: 5px;
  position: relative;
  color: #417acf;
}
.note-attribute-placeholder {
  color: white;
  position: absolute;
  font-size: 10px;
  text-transform: uppercase;
  top: 3px;
  left: 20px;
}
.notification-content {
  margin-left: 50px;
}
input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
input:-webkit-autofill:active {
  transition: background-color 5000s ease-in-out 0s;
}

/* Comments */
.comment {
  line-height: 1;
  border-bottom: 1px solid #dadada;
  padding: 8px 0 8px 20px;
}

/* Buttons */

.icon {
  color: #999898;
  font-size: 17px;
}
.icon-orange {
  color: #ee8723;
  transition-duration: 0.5;
}
.btn {
  border-radius: 3px;
  text-align: center;
  padding: 6px 10px;
  text-decoration: none;
  box-sizing: border-box;
  cursor: pointer;
  font-weight: 500;
}
.btn-link {
  background: none;
  border: none;
  padding: 12px 7px 9px 7px;
  color: rgb(109, 109, 109);
  width: 100%;
  font-size: smaller;
  text-align: left;
  cursor: pointer;
  font-weight: 500;
}
.btn-link:hover {
  background-color: #ebebeb;
  transition-duration: 0.3s;
}
.active {
  border-right: 3px solid #ee8723;
  background-color: #ebebeb;
  color: rgb(58, 58, 58);
  font-weight: bold;
  transition-duration: 0.4s;
}
.link {
  text-decoration: none;
  font-weight: 500;
  color: #3587be;
}
.btn-orange {
  background-color: #ee8723;
  color: white;
  border: 2px solid #ee8723;
}
.btn-blue {
  background-color: #3d47d1;
  color: white;
  border: 2px solid #3d47d1;
}

.btn-grey {
  background-color: grey;
  color: white;
  border: 2px solid grey;
  width: 90%;
}
.btn-dark {
  background-color: #252526;
  color: white;
  border: 2px solid #252526;
}
.btn:hover,
.btn-select:hover {
  transition-duration: 0.4s;
  background-color: #ffffff;
  color: #000000;
  border: 2px solid #ee8723;
}
.btn-group {
  padding: 0%;
  border-radius: 4px;
  width: fit-content;
  overflow: hidden;
}
.btn-select {
  margin: 0;
  border: none;
  border: 2px solid #dadada;
  border-radius: 0px;
  padding: 6px 14px;
  text-decoration: none;
  cursor: pointer;
  font-weight: 500;
}
.btn-group .active {
  transition-duration: 0.4s;
  background-color: #ffffff;
  color: #000000;
  border: 2px solid #ee8723;
}
.tag {
  background-color: #c2e3f7;
  color: #0479bd;
  padding: 4px 10px;
  border: none;
  border-radius: 3px;
  margin: 3px;
  font-size: 12px;
}
.tag:hover {
  background-color: #e2f3fd;
  cursor: pointer;
  transition-duration: 0.4s;
}

/* The Modal*/
.modal {
  display: none;
  position: fixed;
  z-index: 2;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgb(0, 0, 0);
  background-color: rgba(0, 0, 0, 0.4);
}

.modal-content {
  background-color: #fefefe;
  margin: 15% auto;
  height: fit-content;
  padding: 20px 20px 40px 20px;
  border: 1px solid rgb(177, 164, 164);
  box-sizing: border-box;
  width: 40%;
  border-radius: 5px;
}
/* Profile elements */
.profile-stats {
  display: flex;
  justify-content: space-between;
  border: 1px solid #dadada;
  border-radius: 4px;
  line-height: 1;
  padding: 15px 10px;
}
.profile-interest {
  border: 1px solid #dadada;
  border-radius: 4px;
  line-height: 1;
}
.profile-interest-wrapper {
  flex: 40%;
  display: flex;
  justify-content: space-between;
  margin-bottom: 10px;
  margin-right: 10px;
}
.profile-QnA-wrapper {
  display: flex;
  justify-content: space-between;
  padding: 20px;
}
.QnA {
  padding: 5px 11px 7px 11px;
  color: grey;
  border-radius: 3px;
  border: 1px solid #dadada;
  margin-right: 15px;
}
.edit-picture {
  height: 120px;
  width: 120px;
  position: relative;
  border-radius: 5px;
  overflow: hidden;
}
.edit-picture img {
  display: block;
  height: 120px;
  width: 120px;
  object-fit: cover;
}
.edit-picture button {
  background-color: #2d2d42;
  color: #ffffff;
  position: absolute;
  border: none;
  cursor: pointer;
  padding: 5px;
  width: 100%;
  bottom: 0;
  left: 0;
}
.is-answered {
  background-color: #417acf;
  color: #ffffff;
  border: 1px solid #417acf;
}
@media screen and (max-width: 1700px) {
  .header-container,
  .container {
    width: 1250px;
  }
  .left-pane {
    width: calc(1250px * 0.11);
  }
}
@media screen and (max-width: 1400px) {
  .tag-wrapper {
    width: 32%;
  }
  @media screen and (max-width: 1100px) {
    .tag-wrapper {
      width: 48%;
    }
  }
}
@media screen and (max-width: 1250px) {
  .header-container,
  .container {
    width: 97%;
  }
  .content {
    float: none;
    width: 100%;
  }
  .right-pane {
    float: none;
    width: 90%;
    margin: auto;
    margin-top: 0px;
  }
  .left-pane {
    width: 17%;
  }
  .middle-pane {
    width: 83%;
    margin-left: 17%;
  }
  .modal-content {
    width: 60%;
  }
}
@media screen and (max-width: 700px) {
  .modal-content {
    width: 80%;
  }
  .notification-wrapper {
    position: fixed;
    width: 100%;
  }
  header .word {
    display: none;
  }
  body {
    font-size: small;
  }
  .header-container {
    width: 95%;
    justify-content: space-between;
  }
  .left-pane {
    display: none;
  }
  .middle-pane {
    width: 100%;
    margin-left: 0;
  }
  .container {
    width: 100%;
    left: 0;
  }
  .content {
    width: 100%;
  }
  .search-icon {
    font-size: 16px;
  }
  .btn {
    width: 100%;
    margin: 5px 0;
  }
  .header-toggle {
    display: flex;
  }
  .main-nav {
    display: none;
  }
  #mobile-nav {
    display: block;
  }
  .search-bar {
    width: 65%;
  }
  .search-icon {
    top: 5px;
  }
  header .avatar {
    margin-right: 10px;
    width: 23px;
    height: 23px;
  }
  .tag-wrapper {
    width: 100%;
  }
  .edit {
    float: none;
  }
}
